body {
  margin: 0;
  font-family: sans-serif;
  line-height: 1.5;
}

.search-form {
  width: 100%;
  text-align: center;
  text-transform: uppercase;
  background-color: #008b8b;
  justify-content: center;
  margin: 0;
  padding-top: 13px;
  padding-bottom: 13px;
  display: flex;
  position: -webkit-sticky;
  position: sticky;
  top: -1px;
}

.form-input {
  width: 300px;
  filter: drop-shadow(0 5.44492px 5.44492px rgba(0, 0, 0, .03));
  border: 1.5px solid rgba(17, 17, 17, .05);
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
