}

.load-more-hidden {
  display: none;
}

.load-more {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-color: #008b8b;
  border: none;
  border-radius: 5px;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 15px 20px;
  font-size: 20px;
  transition: background-color .3s ease-in-out, transform .2s ease-in-out;
  display: block;
